.home-main-container {
    min-height: calc(100vh - 130px);
    max-width: 1100px;
    padding: 65px 30px 30px 30px;
    margin: auto;
}

.home-motto {
    margin-top: 160px;
    margin-left: 160px;
    position: absolute;
    top: 0;
}

.home-motto-mobile {
    position: absolute;
    width: 100%;
    top: 60px;
    padding: 16px;
    overflow-x: hidden;
}

.home-motto-text {
    color: #667db6;
}

.home-motto-text-mobile {
    color: #667db6;
    text-align: center;
}

.login-container-default {
    display: flex;
    position: absolute;
    top: 160px;
    right: 160px;
    height: 400px;
    justify-content: flex-end;
}

.login-container-mobile {
    height: 80vh;
    padding-left: 16px;
    margin-top: 56px;
    margin-bottom: 50px;
    -webkit-transform: translate3d(0, 0, 0);
}

.conversionPanel{
    margin: 0 30% 0 30%;
}

@media screen and (max-width: 767px) {
    .conversionPanel {
        margin: 0 10% 0 10%;
    }
}

.popup-content {
    width: 30% !important;
    border-radius: 10px;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
}

@media screen and (max-width: 767px) {
    .popup-content {
        width: 70% !important;
    }
}
.conversionHistory{
    display: flex;
    font-size: small;
    background-color: #f1f3f6!important;
    min-height:43vh;
}

.conversionDetails .card{
    margin: 0 5% 0 5%;
}

.ly_conversionHistory{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0px;
    padding-top: 0px;
    flex: 1;
}

@media screen and (max-width: 767px) {
    .ly_trade_history {
        margin-top: 50px;
        margin-right: 10px;
        margin-left: 55px;
        margin-bottom: 15px;
    }
}

.cardHeader {
    background-color: #f1f3f6 !important;
    color: #495057;
}

.cardBody{
    overflow-y: auto;
}

.cardBody::-webkit-scrollbar {
    width: 0.3em;
    height: 0.3em;
}

.cardBody::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.cardBody::-webkit-scrollbar-track-piece{
    background-color: white;
}

.cardBody::-webkit-scrollbar-thumb {
    background-color: #667db6;
    outline: 1px solid #FF8800;
}

.actions {
    margin: auto;
    width: 100%;
    padding: 10px 5px;
    text-align: center;
}
#pageDropDown{
    background-color: #c3e6cb !important;
    color: black !important;
}

.form-error-container {
    margin-top: 4px;
}

.form-error-label {
    color: #FF8800;
}
